*{
    font-family: 'Montserrat', sans-serif !important;
}

.bg-custom {
   /* background : linear-gradient(to right , white , #6e8b3d , #6e8b3d) */
    background : white;
    border-bottom: 0.5px solid lightgray;
}
.top {
    margin-top: 100px;
}
.highlight {
    color : #033860;
}
.bg {
    background: #033860;
}

.customize_btn{
    background: #033860 !important;
}

.imahe{
    width : 400px;
}

footer p {
    margin-top: 100px;
    font-size: 12px
}


p{
    font-size: 16px
}

.demo{
     float: right;

  position: relative;
  left: -50%; /* or right 50% */
  text-align: left;
}
.courses_details{
    font-size: 14px
}
@media (max-width : 768px){
    .btn{
        float: left;
    }
    
    .downloads{
        text-align: center;
    }
    .note{
        padding-top: 50px;
    }
    button{
        font-size: 13px !important;
    }
    label{
        font-size : 13px !important;
    }
    .title {
        margin-top: 50px;
        text-align: center;
        
    }
    .imahe{
        width : 300px;
        
    }
    .ima{
        display: grid;
        align-content: center;
        justify-content: center;
    }
    .bg-custom {
        background : #6e8b3d;
        color : black
    }
    h1{
        font-size : 25px;
        margin-bottom: 10px ;
        text-align: center
    }
    p{
        font-size : 12px ;
        text-align: justify;
    }
    h5{
        font-size: 15px
    }
    
    .btn{
        font-size : 12px !important
    }
    iframe{
    width : 100% !important;
    height : 150px !important;
    border-bottom : 1px solid rgba(0,0,0,.125) !important;
    border-left : 1px solid rgba(0,0,0,.125) !important;
    border-right : 1px solid rgba(0,0,0,.125) !important;
    border-top : 1px solid #fff !important;
   
        
}
    li{
        font-size: 12px
    }
   
}

.admission_border{
    border : 1px solid lightgray ;
    padding : 25px ;
   
}
#banner {
    width : 350px
}
.banner_container {
    display: grid;
    justify-content: center;
    align-items: center
}

textarea{
    border-radius: 5px !important;
    width : 100% !important;
}


input:focus{
   outline: none;
  
    
}


/* todo list */

.todolist_main input{
    padding : 10px !important;
    font-size : 16px !important;
    border : 1px solid lightgray !important;
    width : 100% !important;
   height : 100px;
}

.todolist_main button{
   margin-left: -1px !important;
    padding : 10px;
    font-size : 16px;
    margin : 10px;
    margin-right: 0px;
    background: #ABB557;
    color : white;
    border: none;
     margin-bottom: 50px;
    cursor: pointer
}



.theList li{
    color : white;
    border : 1px solid lightgray;
    padding : 20px;
    list-style: none;
    margin-bottom: 15px;
    width : 100% !important;
    background-color: #033860;
    -webkit-transition : background-color .2s east-out;
    transition : background-color .2s east-out;
    border-radius: 5px;

}


.theList li:hover{
    background: #721114;
    -webkit-transition: 1s;
    transition: 1s;
    opacity: 0.6;
    cursor : pointer

}


ul.theList{
  padding : 0;
   
}


.faded {
    background: #ABB557
}

